import React, {useState} from 'react';
import {Draggable} from "react-beautiful-dnd";
import {Resizable} from "re-resizable";
import styled from 'styled-components'
import {ParagraphForm} from "./ParagraphForm";
import {DrupalContext} from "../WidgetManager";
import {AdminTitleField} from "./Widgets/AdminTitleField";
import {FlexDiv} from "./Atoms/FlexDiv";
import {DrupalModal} from "./Atoms/DrupalModal";
import {DrupalModalFooter} from "./Atoms/DrupalModalFooter";

const ParagraphWrapper = styled.div``;

const ResizehandleWrapper = styled.div`
  width: 5px;
  border-right: 1px solid #000;
  height: 100%;
`;

const ItemIcon = styled.img`
  max-height:35px;
  max-width:35px;
  margin-right: 30px;
`;

const ResizeHandle = () => {
  return (
    <ResizehandleWrapper>
      <span className="visually-hidden">Resize Item</span>
    </ResizehandleWrapper>
  )
};

export const Paragraph = ({item, ...props}) => {

  const [collapsed, setCollapsed] = useState(Object.keys(item.entity).length > 1);

  const getItemIcon = (availableTools) => {
    let iconSrc = 'data:image/png;base64,';
    if (availableTools[item.entity.type[0].target_id].icon) {
      iconSrc = availableTools[item.entity.type[0].target_id].icon;
    }

    return <ItemIcon
      className="item-icon"
      role="presentation"
      alt=""
      src={iconSrc}
    />;
  };

  const onItemResize = () => {

  };

  const openCloseModal = () => {
    setCollapsed(!collapsed);
  };

  return (
    <Draggable
      draggableId={props.id}
      index={props.index}
      isDragDisabled={!props.isDraggable}
    >
      {(provided, snapshot) => (
        <DrupalContext.Consumer>
          {drupalContext =>
            <ParagraphWrapper
              ref={provided.innerRef}
              {...provided.draggableProps}
              isDragging={snapshot.isDragging}
              id={props.id}
            >
              <Resizable
                style={{padding: "10px"}}
                enable={{
                  top: false,
                  right: props.resizableItems,
                  bottom: false,
                  left: false,
                  topRight: false,
                  bottomRight: false,
                  bottomLeft: false,
                  topLeft: false
                }}
                defaultSize={{width: '100%', height: 'auto'}}
                size={{
                  width: item.width * (props.rowWidth / 12)
                }}
                onResizeStop={onItemResize}
                handleComponent={{right: <ResizeHandle/>}}
                grid={[props.rowWidth / 12, 1]}
                minWidth={props.rowWidth / 12 * 2}
                maxWidth={(12 - props.rowItemsWidthTotal + item.width) * (props.rowWidth / 12)}
              >
                <FlexDiv
                  className="move-item-handle"
                  alignItems="center"
                  justifyContent="left"
                  {...provided.dragHandleProps}
                >

                  <FlexDiv
                    justifyContents="left"
                    alignItems="center"
                    style={{
                      color: "#4d4f53",
                      border: "1px solid #BDBDBD",
                      padding: "20px 50px",
                      lineHeight: "35px",
                      maxWidth: "150px",
                      whiteSpace: "nowrap",
                      overflow: "hidden",
                      textOverflow: "ellipsis"
                    }}
                  >
                    {getItemIcon(drupalContext.tools)}

                    {item.admin_title}

                  </FlexDiv>
                  <div>
                    <button
                      type="button"
                      className="button"
                      onClick={openCloseModal}
                      style={{marginLeft: '10px'}}
                    >
                      Edit
                      <span className="visually-hidden">
                          {item.admin_title}
                        </span>
                    </button>
                  </div>
                </FlexDiv>
              </Resizable>

              <DrupalModal
                isOpen={!collapsed}
                onRequestClose={openCloseModal}
                contentLabel={`Edit ${props.typeLabel} > "${item.admin_title}"`}
                wrapperProps={{style: {height: "calc(100% - 109px)"}}}
              >
                <form onSubmit={e => e.preventDefault()}>
                  <div style={{padding: "20px"}}>
                    <AdminTitleField
                      textField={!collapsed}
                      item={item}
                      onChange={drupalContext.onAdminTitleChange.bind(undefined, item.id)}
                    />
                    <ParagraphForm
                      apiUrls={drupalContext.apiUrls}
                      item={item}
                    />
                  </div>
                  <DrupalModalFooter>
                    <input
                      type="submit"
                      className="button button--primary"
                      onClick={(e) => {
                        e.preventDefault();
                        openCloseModal()
                      }}
                      value="Continue"
                    />

                  </DrupalModalFooter>
                </form>
              </DrupalModal>
            </ParagraphWrapper>
          }
        </DrupalContext.Consumer>
      )}
    </Draggable>
  )
};
