import React from 'react';
import {Droppable, Draggable} from "react-beautiful-dnd";
import styled from 'styled-components'
import {DrupalContext} from "../WidgetManager";
import {FlexDiv} from "./Atoms/FlexDiv";

const ToolboxWrapper = styled.div`
  border: 1px solid #C0C0C0;
  background: #F0F0EE;
  padding: 20px;
  max-height: calc(100vh - 120px);
  position: sticky;
  top: 80px;
  margin: -31px -19px -19px 20px;
  width: 200px;
  max-width: 200px;
  overflow: auto;
`;

const Toolwrapper = styled.div`
  background: #fff;
  margin: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.13);
  width: 100px;
  min-height: 75px;
  text-align: center;
  font-weight: 600;
  font-size: 15px;
  line-height:23px;
  color: #4d4f53;

  img {
    max-width: 80px
    max-height: 40px;
    margin: 0 auto 3px;
    display: block;
  }
`;

export const Toolbox = ({tools}) => {
  return (
    <ToolboxWrapper>
      <h3 className="fieldset-legend">Drag and drop Paragraphs into Rows</h3>
      <Droppable
        droppableId="toolbox"
        type="item"
        direction="horizontal"
        isDropDisabled={true}
      >
        {provided => (
          <div
            {...provided.droppableProps}
            ref={provided.innerRef}
          >
            <FlexDiv column={true}>
              {Object.keys(tools).map((tool_key, weight) => (
                <Tool
                  key={`tool-${tool_key}`}
                  weight={weight}
                  machine_name={tool_key}
                  label={tools[tool_key].label}
                  icon={tools[tool_key].icon}
                />
              ))}
              {provided.placeholder}
            </FlexDiv>
          </div>
        )}
      </Droppable>
    </ToolboxWrapper>
  )
};

const Tool = ({weight, machine_name, label, icon}) => {

  if (!icon) {
    icon = 'data:image/png;base64,';
  }

  return (
    <DrupalContext.Consumer>
      {drupalContext =>

        <Draggable
          draggableId={machine_name}
          index={weight}
        >
          {provided => (
            <Toolwrapper
              ref={provided.innerRef}
              {...provided.draggableProps}
              {...provided.dragHandleProps}
              onDoubleClick={(e) => drupalContext.addToolToBottom(machine_name, e)}
              id={`tool-${machine_name}`}
            >
              <img src={icon} alt="" role="presentation"/>

              <div className="label">{label}</div>
            </Toolwrapper>
          )}
        </Draggable>

      }
    </DrupalContext.Consumer>
  )

};
